@font-face {
  src: url('digital-7.regular.ttf');
  font-family: 'digital';
}

.app {
  background-color: #5E5C72;
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.calculator {
  font-size: 77px;
  font-family: 'digital';
  display: grid;
  grid-template-rows: min-content auto;
  grid-template-columns: minmax(600px, 800px);
  width: 68%;
  height: 100%;
  margin: auto;
  justify-content: center;
}

.screen {
  align-self: start;
  text-align: right;
  padding: 22px;
  color: #3C4B02;
  background-color: #7D9C03;
  border: 4px solid black;
  border-bottom: 15px solid black;
}

.screen__result {
  font-size: 140px;
  margin-bottom: 22px;
}

.button {
  background-color: black;
  color: darkgray;
  border-radius: 77px;
  font-size: unset;
  font-family: unset;
}

.button--clear {
  background-color: brown;
}

.button--equal {
  background-color: darkslategray;
}

.button--operator {
  background-color: dimgray;
}

.button:hover {
  background-color: #252525;
  color: white;
}

.panel {
  border-top: 8px solid #5E5C72;
  display: grid;
  grid-template-areas:
    "AC AC DIV MUL"
    "d7 d8 d9 SUB"
    "d4 d5 d6 ADD"
    "d1 d2 d3 EQU"
    "d0 d0 DEC EQU";
}

.clear {
  grid-area: AC;
}
.zero {
  grid-area: d0;
}
.one {
  grid-area: d1;
}
.two {
  grid-area: d2;
}
.three {
  grid-area: d3;
}
.four {
  grid-area: d4;
}
.five {
  grid-area: d5;
}
.six {
  grid-area: d6;
}
.seven {
  grid-area: d7;
}
.eight {
  grid-area: d8;
}
.nine {
  grid-area: d9;
}
.add {
  grid-area: ADD;
}
.subtract {
  grid-area: SUB;
}
.divide {
  grid-area: DIV
}
.multiply {
  grid-area: MUL;
}
.decimal {
  grid-area: DEC;
}
.equals {
  grid-area: EQU;
}

@media (max-width: 603px) {
  .calculator {
    grid-template-rows: unset;
    grid-template-columns: unset;
    justify-content: unset;
    margin: unset;;
    width: 100%;
    font-size: 34px;
  }
  .screen__result {
    font-size: 70px;
  }
}

@media (max-width: 238px) {
  .calculator {
    font-size: 18px;
    grid-template-rows: max-content;
  }
  .screen__result {
    font-size: 40px;
  }
}